.blogCardWrap {
  text-align: left;
  border: 0px;
  background: transparent;
  margin-bottom: 3rem;
  cursor: pointer;
  border-radius: 4px;
  padding: 0;
  transition: 0.3s;
  margin-bottom: 3rem; }
  .blogCardWrap:hover {
    transform: translateY(-5px) scale(1.01); }

.blogCard {
  display: flex; }

.blogCardImg {
  align-self: start; }

.blogCardCnt {
  margin-left: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.blogCardCntBd > h6 {
  font-size: 32px;
  color: var(--primary-main); }

.subtitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-weight: 600; }

[data-query="T"] {
  display: none; }

.blogCardAction {
  font-weight: 900;
  font-size: 36px;
  text-align: right;
  margin-top: 14px;
  color: var(--primary-main); }

.blogPagination {
  display: flex;
  justify-content: center; }
  .blogPagination > a {
    transition: 0.2s;
    padding: 12px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-right: 0px;
    color: black; }
    .blogPagination > a:hover {
      background-color: black;
      color: white; }
    .blogPagination > a:last-child {
      border-right: 1px solid black; }

@media (min-width: 768px) and (max-width: 979px) {
  .blogCardImg {
    width: 40%; }
  .blogCardCnt {
    margin-right: 30px; }
  .blogCardCntBd > p {
    font-size: 14px;
    position: relative; }
  .blogCardCntBd > h6 {
    font-size: 20px; }
  .subtitle {
    font-size: 14px;
    margin-bottom: 12px; }
    .subtitle > h6 {
      font-size: 16px; }
  .blogCardAction {
    display: none; }
  [data-query="T"] {
    display: inline-block;
    margin-left: 8px;
    font-weight: 700;
    font-size: 14px; } }

@media (max-width: 767px) {
  .blogCard {
    flex-direction: column;
    align-items: stretch;
    margin-bottom: 20px; }
  .blogCardImg {
    margin-bottom: 8px; }
  .blogCardCnt {
    margin-left: 0; }
  .blogCardAction {
    display: none; }
  .blogCardCntBd > p {
    display: none; }
  .subtitle {
    margin-bottom: 0; } }
